.App{
  min-height: 100vh;
  background: rgb(2,0,36);
  background: linear-gradient(329deg, rgba(2,0,36,1) 8%, rgba(95,221,179,1) 49%);
}

.container{
  width: 350px;
  margin: 0 auto;
  padding-top: 200px;
}

.generator{
  background:#5d47d9;
  border-radius: 5px;
  box-shadow: 0px 2px 10px rgb(17, 37, 82);
  padding:22px;

}

.generator_header{
  text-align: center;
  color: white;
  margin-bottom:20px;
}

.generator_password{
  position: relative;
  background:#281f66;
  padding:13px;
  color:white;
  height: 50px;
  margin-bottom: 17px;
}

.copy_btn{
  position: absolute;
  background:#4844bd;
  color:white;
  border:none;
  height: 40px;
  padding:10px;
  cursor:pointer;
  top:4px;
  right:4px;
  transition: all 0.2s ease;
}
.copy_btn:hover{
  background:rgb(39, 227, 39);
}

.generator_btn{
  background: #281f66;
  border:none;
  display: block;
  width: 100%;
  padding:10px;
  color:white;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.generator_btn:hover{
  background:rgb(39, 227, 39);
  color:black;
}

.form-group{
  display: flex;
  justify-content: space-between;
  color:white;
  margin-bottom: 15px;

}

@media(max-width:450px){
  .container{
    display: flex;
    position: relative;
    width: 250px;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    padding-top: 200px;
  }

}